"use client";

import BoletinPDF from "./boletin/BoletinPDF";
import { useAuth } from "../../context/authContext";
import styles from "../../styles/estudiante-dashboard.module.css";
import { useState } from "react";
import Link from 'next/link';

export default function EstudianteDashboard() {
  const { user, loading } = useAuth();
  const [cicloId] = useState(1); // Cambia esto por el ciclo activo real

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <h2>Cargando...</h2>
      </div>
    );
  }

  if (!user) {
    return (
      <div className={styles.loadingContainer}>
        <h2>Bienvenido a AulaUnida</h2>
        <p>Por favor inicia sesión para ver tu información.</p>
      </div>
    );
  }

  return (
    <div className={styles.dashboardContainer}>
      <h1 className={styles.dashboardTitle}>Panel del Estudiante</h1>
      <div className={styles.modulesRow}>
        {/* Módulo de Horario */}
        <Link href="/estudiante/horario" className={styles.moduleBox}>
          <h2 className={styles.moduleTitle}>Horario Escolar</h2>
          <p className={styles.moduleDesc}>Aquí verás tu horario de clases.</p>
        </Link>
        {/* Módulo de Notas */}
        <Link href="/estudiante/notas" className={styles.moduleBox}>
          <h2 className={styles.moduleTitle}>Notas</h2>
          <p className={styles.moduleDesc}>Consulta tus notas por materia y periodo.</p>
        </Link>
      </div>
      {/* Botón para descargar boletín */}
      <div className={styles.boletinBtnContainer}>
        <BoletinPDF estudianteUid={user.uid} cicloId={cicloId} />
      </div>
    </div>
  );
}